
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">公告管理</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">
            <el-row>
                <el-col :span="4" class="el-col"  style="margin-left: 0">
                    <p class="input-title">公告标题:</p>
                    <el-input  v-model="searchForm.title" placeholder="请输入公告标题">
                    </el-input>
                </el-col>

                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-search" @click = "search">查询</el-button>
                </el-col>
                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-plus"  @click = "addruntime">新增</el-button>
                </el-col>

            </el-row>
        </el-col>

        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th>序号</th>
                    <th>公告类型</th>
                    <th>公告标题</th>
                    <th>公告内容</th>
                    <th>创建时间</th>
                    <th>更新时间</th>
                    <th>操作</th>
                </tr>
                <tr v-for="(item,index) in aaData.data">
                    <td>{{index+1}}</td>
                    <td>{{item.typeName}}</td>
                    <td style="max-width: 200px;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{item.title}}</td>
                    <td style="max-width: 200px;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{item.content }}</td>
                    <td>{{item.createTime}}</td>
                    <td>{{item.updateTime }}</td>
                    <td>
                        <span class="dothis" @click="seeDetail(item.id)">查看</span>
                        <span class="dothis" @click="edit(item.id,item.type,item.title,item.content)">编辑</span>
                        <span class="dothis" @click="del(item.id)">删除</span>
                    </td>
                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%">

                <el-pagination
                    style="position: absolute;right: 0"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    :page-sizes="[10, 20, 30, 40]"
                    layout="total,prev, pager, next, jumper"
                    :total="aaData.totalCount">
                </el-pagination>
            </div>
            <div style="height: 80px"></div>
        </el-col>

        <el-dialog title="新增公告" :visible.sync="addvisible">
            <el-form :model="addForm">
                <el-form-item label="公告类型" :label-width="formLabelWidth">
                    <el-select  v-model="addForm.type"  placeholder="请选择公告类型"  style="width: 400px">
                        <el-option
                            v-for="item in typeOption"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="公告标题" :label-width="formLabelWidth">
                    <el-input  v-model="addForm.title" auto-complete="off " style="width: 150px" placeholder="请输入公告标题"></el-input>
                </el-form-item>
                <el-form-item label="详细内容" :label-width="formLabelWidth" >
                    <el-input type="textarea" v-model="addForm.content"  :rows="5" ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addvisible = false">取 消</el-button>
                <el-button type="primary" @click="addsure" >确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="编辑公告" :visible.sync="editvisible">
            <el-form :model="editForm">
                <el-form-item label="公告类型" :label-width="formLabelWidth">
                    <el-select  v-model="editForm.type"  placeholder="请选择公告类型"   style="width: 400px">
                        <el-option
                            v-for="item in typeOption"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="公告标题" :label-width="formLabelWidth">
                    <el-input  v-model="editForm.title" auto-complete="off " style="width: 150px" placeholder="请输入公告标题"></el-input>
                </el-form-item>
                <el-form-item label="详细内容" :label-width="formLabelWidth" >
                    <el-input type="textarea" v-model="editForm.content" :rows="5"></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="editvisible = false">取 消</el-button>
                <el-button type="primary" @click="editsure" >确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="公告详情" :visible.sync="detailvisible">
            <p style="text-align: center;font-size: 18px;font-weight: 600">{{title}}</p>
            <p style="margin-top: 20px">{{content}}</p>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="detailvisible = false" >确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    export default {
        data() {
            return {
                value:'',
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                totalNum:10,//总条数
                aaData:[],
                addvisible:false,
                editvisible:false,
                detailvisible:false,
                typeOption:[{
                    id:1,
                    name:"停复航通知"
                }],
                searchForm:{
                    title:"",
                },
                addForm: {
                    type:"",
                    title:"",
                    content:"",
                },
                editForm: {
                    type:"",
                    title:"",
                    content:"",
                    id:""
                },
                formLabelWidth: '120px',
                title:"",
                content:""
            };
        },
        methods: {


            getRunTimeList:function () {
                var data  = this.searchForm
                data.pageIndex=this.currentPage
                data.pageSize=this.pageSize

                for(var i in data){
                    if(String(data[i])==""){
                        delete data[i]
                    }
                }

                ajax.announcementList(data,res=>{
                    if(res){
                        this.aaData=res.data.result
                    }
                })
            },
            addruntime(){
                this.addvisible = true
                for(var i in this.addForm){
                    this.addForm[i]=""
                }

            },
            addsure(){
                for(var i in this.addForm){
                    if(String(this.addForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填项',
                            type: 'fail'
                        });
                        return
                    }
                }

                ajax.announcementAdd(this.addForm,res=>{
                    if(res){
                        this.addvisible = false
                        this.getRunTimeList()
                    }
                })
            },

            search:function () {
                this.currentPage = 1
                this.getRunTimeList ()

            },
            edit(id,type,title,content){
                this.editvisible=true
                this.editForm={
                    id:id,
                    type:type,
                    title:title,
                    content:content
               }
            },
            editsure(){
                for(var i in this.editForm){
                    if(String(this.editForm[i])==""){
                        this.$message({
                            showClose: true,
                            message: '所有输入框为必填项',
                            type: 'fail'
                        });
                        return
                    }
                }
                ajax.announcementEdit(this.editForm,res=>{
                    if(res){
                        this.editvisible=false
                        this.getRunTimeList()
                        this.$message({
                            showClose: true,
                            message: '更新成功',
                            type: 'success'
                        });
                    }
                })
            },
            del(id){
                this.$confirm('是否继续删除操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    ajax.announcementDel({id:id},res=>{
                        if(res){
                            this.getRunTimeList()
                            this.$message({
                                showClose: true,
                                message: '删除成功',
                                type: 'success'
                            });
                        }
                    })

                }).catch(() => {

                });
            },
            seeDetail(id){
                this.detailvisible = true
                ajax.announcementDetail({id:id},res=>{
                    if(res){
                        this.title = res.data.result.title
                        this.content = res.data.result.content
                    }
                })
            },
            handleSizeChange(val) {
                this.pageSize = val
                this.getRunTimeList ()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.getRunTimeList ()
            }
        },
        mounted(){
            this.getRunTimeList()
        }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



